.swagger-text {
    font-weight: bold;
    margin-bottom: 10px;
}

.swagger-text-link {
    text-decoration: none;
}

.swagger {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.autenticado {
    color: green;
    font-weight: bold;
    padding: 5px 10px;
}

.nao-autenticado {
    color: red;
    font-weight: bold;
    padding: 5px 10px;
}

.react-tabs__tab-list {
    margin-top: 10px;
    width: 100%;
    display: flex;
    cursor: pointer;
    justify-content: space-between;
    background-color: rgba(0, 0, 0, 0.3);
}

.react-tabs__tab--selected {
    background-color: rgba(0, 0, 0, 0.2);
}

.react-tabs__tab {
    padding: 5px;
    list-style: none;
    width: 100%;
}